          border-top-right-radius: 50px;
          background: $white;
        }
      }

      &:hover:before {
        content: '';
        position: absolute;
        width: 10px;
        height: 6px;
        bottom: -1px;
        margin: 0 auto;
        left: 0;
        right: 0;
        z-index: 1;
        display: block;
        border-top-left-radius: 50px;
        border-top-right-radius: 50px;
        background: $white;
      }
    }

    &:hover {
      border-right: 1px solid #e3e3f7 !important;
    }

    a:hover {
      color: $white;
      position: relative;
    }
  }
}

.sidenav-toggled .resp-vtabs li.active:before {
  display: none;
}

